body {
  background-image: url(/Portfolio/static/media/background.7d117529.png);
  background-size: contain;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  max-width: 100%;
  overflow-x: hidden; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace; }

.app {
  text-align: center; }

.container {
  display: flex;
  justify-content: center; }

.container {
  display: block;
  margin: 20px 40px; }

.title {
  background-color: #27ade3;
  color: white;
  font-size: 26px;
  padding: 7px;
  padding-left: 20px;
  text-align: left; }

.subTitle {
  display: flex;
  color: gray;
  margin-top: -10px; }

.place {
  width: 50%;
  text-align: left; }

.charge {
  display: flex;
  font-size: 24px;
  margin: 10px 0px; }

.time {
  width: 50%;
  text-align: right; }

.link {
  text-decoration: none;
  color: black; }

.description {
  margin-top: 0px;
  margin-bottom: 5px;
  text-align: justify; }

